<template>
  <el-dropdown placement="top" class="tabs-item lang" @command="switchLang">
    <span class="el-dropdown-link" v-show="show">
      <i class="icon" v-if="showIcon"></i>
      {{ lang }}
      <i class="el-icon-arrow-down el-icon--right"></i>
    </span>
    <el-dropdown-menu slot="dropdown">
      <el-dropdown-item command="cn">中文</el-dropdown-item>
      <el-dropdown-item command="en">
        English
      </el-dropdown-item>
      <el-dropdown-item command="ja">
        日本语
      </el-dropdown-item>
      <el-dropdown-item command="ko">
        한국어
      </el-dropdown-item>
      <el-dropdown-item command="vi">Tiếng Việt</el-dropdown-item>
    </el-dropdown-menu>
  </el-dropdown>
</template>

<script>
export default {
  name: 'Language',
  props: {
    show: {
      type: Boolean,
      default: false,
    },
    showIcon: {
      type: Boolean,
      default: true,
    },
  },
  data() {
    return {
      lang: 'en',
      langList: {
        en: 'English',
        cn: '中文',
        ja: '日本语',
        ko: '한국어',
        vi: 'Tiếng Việt',
      },
    }
  },
  mounted() {
    console.log('lang', this.show)
    this.lang = this.langList[localStorage.getItem('lang') || 'en']
  },
  methods: {
    switchLang(lang) {
      console.log(lang)
      this.lang = this.langList[lang]
      localStorage.setItem('lang', lang)
      this.$i18n.locale = lang
    },
  },
}
</script>

