<template>
  <div class="header" :style="bg">
    <div class="header-left">
      <div
        @click="handleClick"
        tag="div"
        to="/"
        class="header-logo"
        v-if="scrollTop > 154"
      >
        <div class="logo-title">DECA.FINANCE</div>
      </div>
      <BaseLanguage :show="scrollTop <= 154" v-else />
    </div>
    <BaseMenu :show="showLanguage || scrollTop > 154" />
  </div>
</template>

<script>
import BaseMenu from '@/components/Base/BaseMenu.vue'
import BaseLanguage from '@/components/Base/BaseLanguage.vue'
export default {
  name: 'Header',
  components: {
    BaseMenu,
    BaseLanguage,
  },
  props: {
    msg: String,
    show: false,
    showLanguage: false,
  },
  data() {
    return {
      scrollTop: 100,
    }
  },
  computed: {
    bg() {
      if (this.show) {
        return 'background: #0D3366'
      }
      if (this.scrollTop > 154) {
        return 'background: #0D3366'
      }
    },
  },
  mounted() {
    console.log('header', this.showLanguage)
    window.addEventListener('scroll', this.handleScroll, true)
  },
  methods: {
    handleScroll(e) {
      this.scrollTop =
        e.target.documentElement.scrollTop || e.target.body.scrollTop
    },
    handleClick() {
      if (this.$route.name === 'Home') {
        window.scrollTo(0, 0)
      }
    },
  },
}
</script>

