<script>
// @ is an alias to /src
import BaseHeader from '@/components/Base/BaseHeader'
import CryptoJS from 'crypto-js'

export default {
  name: 'Home',
  components: {
    BaseHeader,
  },

  mounted() {
    window.atob(
      '',
    )

    // let msg = this.decrypt(
    //   '',
    // )
    let msg = this.decrypt1(
      '',
    )

    console.log('msg', JSON.parse(msg))
  },
  methods: {
    decrypt1(word) {
      var keyStr = '!@$3g0hgl7Ao2OK#'
      var ivStr = '!@$3g0hgl7Ao2OK#'
      var key = CryptoJS.enc.Utf8.parse(keyStr)
      var iv = CryptoJS.enc.Utf8.parse(ivStr)

      // var encryptedHexStr = CryptoJS.enc.Utf8.parse(word)
      // let base64 = CryptoJS.enc.Base64.parse(word)
      var srcs = CryptoJS.enc.Base64.parse(word)
      console.log('SRCS', srcs.toString())
      var decrypt = CryptoJS.AES.decrypt(word, key, {
        iv,
      })
      console.log(decrypt.toString(CryptoJS.enc.Utf8))
      var decryptedStr = decrypt.toString(CryptoJS.enc.Utf8)
      console.log(123, decryptedStr)
      return decryptedStr
    },
    decrypt(word) {
      var keyStr = '0160528EFB73E591835DCBF630696C07'
      var ivStr = '1234567890ABCDEF1234567890ABCDEF'
      var key = CryptoJS.enc.Utf8.parse(keyStr)
      var iv = CryptoJS.enc.Utf8.parse(ivStr)

      // var encryptedHexStr = CryptoJS.enc.Utf8.parse(word)
      // let base64 = CryptoJS.enc.Base64.parse(word)
      var srcs = CryptoJS.enc.Base64.parse(word)
      console.log('SRCS', srcs.toString())
      var decrypt = CryptoJS.AES.decrypt(srcs.toString(), key, {
        iv,
        mode: CryptoJS.mode.CBC,
        padding: CryptoJS.pad.ZeroPadding,
      })
      console.log(decrypt.toString(CryptoJS.enc.Utf8))
      var decryptedStr = decrypt.toString(CryptoJS.enc.Utf8)
      console.log(123, decryptedStr)
      return decryptedStr
    },
  },
}
</script>

